<template>
  <div style="margin-top: 10px">
    <el-collapse v-model="activeNames" accordion>
      <el-collapse-item v-bind:key="index" v-for="(notice,index) in notices" :title="notice.title"
                        :name="notice.id">
        <div>{{notice.message}}</div>
        <div>
          <el-tag></el-tag>
          {{ notice.createDate}}
        </div>
        <div>
          <el-popconfirm
            confirmButtonText=''
            cancelButtonText=''
            @onConfirm="deleteNotice(notice.id)"
            icon="el-icon-info"
            iconColor="red"
            title=""
          >
            <el-button type="danger" size="mini"
                       slot="reference">
            </el-button>
          </el-popconfirm>
        </div>
      </el-collapse-item>
    </el-collapse>
    <el-button icon="el-icon-search" size="mini" type="primary" @click="publishNotice">
    </el-button>
    <el-dialog
      :before-close="handleClose"

      :visible.sync="showPublishNotice"
      title=""
      width="30%">
      <div>
        <el-form :model="notice" :rules="rules" class="demo-ruleForm" label-width="100px"
                 ref="notice"
                 status-icon>
          <el-form-item label="" prop="title">
            <el-input autocomplete="off" type="text" v-model="notice.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="message">
            <el-input autocomplete="off" type="text" v-model="notice.message"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="submitForm('notice')" type="primary"></el-button>
            <el-button @click="resetForm('notice')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "SysNotice"
    ,
    data() {
      return {
        //
        activeNames: [],
        showPublishNotice: false,
        notice: {
          title: '',
          message: ''
        },
        posted: false,
        notices: [],
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
            {min: 0, max: 64, message: ' 0  64 ', trigger: 'blur'}
          ],
          message: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      initNotices() {
        this.getRequest('/sys/nf/').then(resp => {
          if (resp) {
            this.notices = resp.obj.data;
          }
        })
      }, deleteNotice(id) {
        this.deleteRequest('/sys/nf/?mid=' + id).then(resp => {
          if (resp) {
            this.initNotices()
          }

        })
      },
      handleClose(done) {
        this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => {
        });
      },
      publishNotice() {
        this.showPublishNotice = true
      }
      ,
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
      ,
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //websocket
            let msgObj = {};
            msgObj.title = this.notice.title;
            msgObj.message = this.notice.message;
            this.$store.state.stomp.send('/im/send', {}, JSON.stringify(msgObj));
            this.showPublishNotice = false;
          } else {
            return false;
          }
        })
        //
        setTimeout(this.initNotices
          , 1000);
        // this.postRequest("/sys/nf/", this.notice).then(resp => {
        //   if (resp && resp.status === 200) {
        //     this.showPublishNotice = false;
        //     this.initNotices()
        //   }
        // })
      }

    },
    mounted() {
      this.initNotices();
    }
  }
</script>

