<template>
  <div style="margin-top: 10px">
    <span v-if="notices.length===0"></span>
    <!---->
    <el-collapse v-model="activeNames" @change="handleChange" accordion>
      <el-collapse-item v-bind:key="index" v-for="(notice,index) in notices" :name="notice.id">
        <template slot="title">
          <el-tag v-if="notice.state===0"></el-tag>
          <el-tag v-if="notice.state===1" type="success"></el-tag>
          {{notice.title}}
        </template>
        <div>{{notice.message}}</div>
        <div>:{{notice.createDate}}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  export default {
    name: "MyNotice"
    ,
    data() {
      return {
        //
        activeNames: [],
        notice: {
          title: '',
          message: ''
        },
        posted: false,
        notices: [],
      }
    },
    methods: {
      initNotices() {
        this.getRequest('/system/config/notice').then(resp => {
          if (resp) {
            this.notices = resp.obj.data;
          }
        })
      },
      handleChange(val) {
        // valID
        this.posted = !this.posted;
        if (this.posted) {
          let url = '/system/config/notice/' + val;
          this.putRequest(url).then(resp => {
            this.initNotices()
          })
        }
      }
    }, mounted() {
      this.initNotices();
    }
  }
</script>

